.wrapper{
    margin-bottom:50px;
    padding:0 20px;
}
.myresult{
    text-align:left;
    margin-top:20px;
    line-height:170%;
    font-size: 21px;
}
.myresult span{
    font-weight: 800;
    color:#333;
}
.title{
    margin:30px 0;
    font-size:21px;
    font-weight: 800;
    line-height:180%;
    letter-spacing:1em;
    white-space: nowrap;
}
.content{
    display:flex;
    white-space: nowrap;
}
.content ul li{
    margin-bottom:20px;
}
.contentleft{
    text-align:justify;
    
}
.contentright{
    text-align:left;
    margin-left:50px;
}
.subtitle{
    font-weight: bold;
}
.reason{
    text-align:left;
    line-height:170%;
}